 .dropdown-content {
  display: none;
  position: absolute;
  background-color: #34495e;
  background-color: var(--darkBlue);
  min-width: 160px;
  z-index: 1;
}


.dropdown-content button {
  float: none;
  color:white;
  padding: 6px 9px;
  text-decoration: none;
  display: block;
  text-align: left;
  border:none;
  width:100%;
  height:35px;
  background-color: #34495e;
  background-color: var(--darkBlue);
  font-size: 15px;
  /* transition: 0.3s ease-in; */
}

.dropdown-content button:hover {
  background-color: #1abc9c;
  border-radius: 8px;
}

.dropdown:hover .dropdown-content {
  display: block;
}
